'use strict';

import { DbService } from './db-service.js';
import { Utils } from './utilsService.js';
import axios from 'axios';

const KEY = 'books';
const BOOKS_API_KEY = 'apiBookDB'

export default {
  query,
  get,
  remove,
  save,
  addReview,
  deleteReview,
  getNetworkBooks,
  addGoogleBook,
  getNextBook,
  getPrevBook

}
async function getNetworkBooks(bookName) {
  const url = `https://www.googleapis.com/books/v1/volumes?printType=books&q=${bookName}`
  const booksFromApi = (await axios.get(url)).data.items
  console.log(booksFromApi);
  return booksFromApi

}
function addGoogleBook(bookToAdd) {
  const book = {
    id: bookToAdd.id || Utils.getRandomId(),
    title: bookToAdd.volumeInfo.title || 'new book',
    subtitle: bookToAdd.volumeInfo.subtitle || 'rfnkgkrnr',
    authors: bookToAdd.volumeInfo.authors || 'david levi',
    publishedDate: bookToAdd.volumeInfo.publishedDate || '2020',
    description: bookToAdd.volumeInfo.description || 'fjhndvknedjenjdhhb dhebedsnjdsnxkks',
    pageCount: bookToAdd.volumeInfo.pageCount || '100',
    categories: bookToAdd.volumeInfo.categories || 'fjjndn',
    listPrice: {
      amount: 109,
      // currencyCode: bookToAdd.saleInfo.country || "EUR",
      currencyCode:"EUR",
      isOnSale: bookToAdd.saleInfo.isEbook || false

    },
    thumbnail: bookToAdd.volumeInfo.imageLinks.thumbnail || "http://coding-academy.org/books-photos/20.jpg"
  }
  return DbService.post(KEY, book)


}

async function query() {
  var books = await DbService.query(KEY);
  if (!books || !books.length) {
    books = _createDefaultBooks();
    await DbService.insert(KEY, books);
  }
  return books;
}

function get(bookId) {
  console.log('here', bookId);
  return DbService.get(KEY, bookId);
}

function remove(bookId) {
  return DbService.delete(KEY, bookId);
}

function save(bookId) {
  if (book._id) return DbService.put(KEY, book);
  else return DbService.post(KEY, book)
}
function addReview(reviewToAdd, book) {
  console.log('hhhhh');
  reviewToAdd._id = Utils.getRandomId()
  reviewToAdd.datePicker=Date.now()
  const copyBook = { ...book }
  if (copyBook.reviews) copyBook.reviews = [...copyBook.reviews, reviewToAdd]
  else copyBook.reviews = [reviewToAdd]
  return DbService.put(KEY, copyBook)
}
function deleteReview(reviewId, book) {
  console.log('reviewid service', reviewId);
  const copyBook = { ...book }
  copyBook.reviews = copyBook.reviews.filter(review => review._id !== reviewId)
  return DbService.put(KEY, copyBook)
}



