async function getNextBook(bookId) {
  console.log('bookId',bookId);
  const books=await query()
  console.log('books get next',books);
  const nextBook = books.findIndex(book => book._id === bookId)
  console.log('next',nextBook);
  if(nextBook+1===books.length)return books[0]._id;
  else return books[nextBook + 1]._id
  
}
async function getPrevBook(bookId) {
  const books= await query()
  console.log('books get prev',books);
  const prevBook = books.findIndex(book => book._id === bookId)
  if(prevBook===0)return books[books.length-1]._id
  return books[prevBook - 1]._id

}

